<template lang="html">
  <div class="main-wrapper">
    <gallery></gallery>
    <slide-section :title="title_doctor_m"></slide-section>
    <slide-section :title="title_doctor"></slide-section>
    <slide-section :title="title_hospital"></slide-section>
    <slide-section :title="title_health"></slide-section>
    <aside-component></aside-component>
    <footer-component></footer-component>
  </div>
</template>

<script>
import Gallery from '../components/Gallery'
import SlideSection from '../components/SlideSection'
import FooterComponent from '../components/Footer'
import AsideComponent from '../components/Aside'

export default {
  name: 'index_m',
  data(){
    return {
      title_doctor_m: '知名蒙医',
      title_doctor: '青城名医',
      title_hospital: '医院预约',
      title_health: '健康体检'
    }
  },
  components: {
    Gallery,
    SlideSection,
    FooterComponent,
    AsideComponent
  }
}
</script>

<style lang="css" scoped>
  .main-wrapper{
    width: 100%;
    max-width: 980px;
    margin: 0 auto;
    box-sizing: border-box;
  }
</style>



