<template lang="html">
  <div class="main-wrapper">
  <section class="section-container">
    <div class="list-title">
      <span class="title-left">{{title}}</span>
      <span class="title-right">更多</span>
    </div>
    <ul class="section-list">
      <li><img src="../assets/imgs/banner1.jpg" alt=""></li>
      <li><img src="../assets/imgs/banner2.jpg" alt=""></li>
      <li><img src="../assets/imgs/banner1.jpg" alt=""></li>
      <li><img src="../assets/imgs/banner2.jpg" alt=""></li>
      <li><img src="../assets/imgs/banner3.jpg" alt=""></li>
      <li><img src="../assets/imgs/banner4.jpg" alt=""></li>
    </ul>
  </section>
  </div>
</template>

<script>
export default {
  name: 'SlideSection',
  data(){
      return{

      }
  },
  props: ['title']
}
</script>

<style lang="css" scoped>
  @font-face {
      font-family: "mo";
      src: url("../themes/fonts/mnglwhiteaat.ttf");
  }
  *{
    padding: 0;
    margin: 0;
    list-style: none;
    text-decoration: none;
    scroll-behavior: smooth;
    font-family: 'mo', "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }
  body{
    background-color: white;
  }

  .main-wrapper{
    width: 100%;
    max-width: 980px;
    margin: 0 auto;
    padding-left: 10px;
    box-sizing: border-box;
  }

  .section-container{
    border-bottom: 1px solid #999;
    padding-bottom: 10px;
  }
  .list-title{
    padding: 15px 0;
  }
  .title-right{
    float: right;
    font-size: 12px;
    padding-right: 20px;
    /*padding-top: 7px;*/
    color: #777;
    position: relative;
  }
  .title-right::after{
    content: '';
    background: url(../assets/imgs/arrow.png) no-repeat right center;
    width: 10px;
    height: 10px;
    background-size: cover;
    width: 9px;
    height: 9px;
    position: absolute;
    left: 25px;
    top: 4px;
  }
  .section-list{
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    /*padding-left: 10px;*/
    -webkit-overflow-scrolling: touch;
  }
  .section-list li{
    display: inline-block;
    width: 150px;
    height: 160px;
    margin-left: 10px;
  }
  .section-list li:first-child{
    margin-left: 0;
    width: 80px;
    height: 160px;
  }
  .section-list li img{
    width: 100%;
    height: 100%;
    border: 1px solid transparent;
    border-radius: 20px;
  }
  .hospital-list li{
    width: 150px;
    height: 160px;
  }

  aside{
      position: fixed;
      bottom: -60px;
      right: 6px;
      transform: rotate(90deg);
      transform-origin: right top;
    }
    .menu-trigger{
      display: inline-block;
      line-height: 1;
      white-space: nowrap;
      cursor: pointer;
      border: 1px solid #bfcbd9;
      color: #1f2d3d;
      -webkit-appearance: none;
      text-align: center;
      box-sizing: border-box;
      outline: none;
      margin: 0;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      padding: 10px 15px;
      font-size: 14px;
      border-radius: 4px;
      background: #eefff5 url(../assets/imgs/menu.svg) no-repeat 7% center;
      background-size: 26px 26px;
      padding-left: 40px;
    }
    .menu-group{
      margin-top: 10px;
    }
    .menu-item{
      display: block;
      margin-top: 5px;
      background-color: red;
      border-radius: 15px;
      padding: 5px 10px;
      transition: all 3s ease;
      opacity: 0;
    }
    .jump_in{
      animation: jump 0.9s  linear  alternate forwards;
      transition: all 3s;
    }
    .jump_out{
      animation: jump 0.9s  linear  reverse backwards;
      transition: all 3s;
    }
    @keyframes jump {
      0%{
        transform: translateX(-50px);
        opacity: 0;
      }
      50%{
        opacity: 0.5;
        transform: translateX(-30px);
      }
      100%{
        transform: translateX(0);
        opacity: 1;
      }
    }
</style>



